<template>
  <!-- 业务记录 -->
  <div class="main_con_box">
    <div class="top_box">
      <div class="top_box_left">
        <el-select
          class="top_sel"
          size="mini"
          v-model="businessType"
          placeholder="业务类型"
        >
          <el-option
            v-for="item in businessTypelist"
            :key="item.value1"
            :label="item.label"
            :value="item.value1"
          >
          </el-option>
        </el-select>
        <el-select
          class="top_sel"
          size="mini"
          v-model="salesmanId"
          placeholder="业务员"
        >
          <el-option
            v-for="item in salesmanlist"
            :key="item.value1"
            :label="item.label"
            :value="item.value1"
          >
          </el-option>
        </el-select>
        <el-select
          class="top_sel"
          size="mini"
          v-model="customerId"
          placeholder="客户"
        >
          <el-option
            v-for="item in customerlist"
            :key="item.value1"
            :label="item.label"
            :value="item.value1"
          >
          </el-option>
        </el-select>

        <el-date-picker
          v-model="followup_date"
          size="mini"
          style="width: 300px"
          type="daterange"
          range-separator="至"
          start-placeholder="操作日期"
          end-placeholder="操作日期"
        >
        </el-date-picker>
        <el-button
          class="search_btn"
          type="primary"
          size="mini"
          @click="search()"
          >查 询</el-button
        >
        <el-button size="mini" @click="resetForm()">重 置</el-button>
      </div>
    </div>

    <div class="main_box">
      <el-table
        :data="tableData"
        border
        style="width: 100%"
        :height="$store.state.innerheight - 215"
        size="mini"
        :header-cell-style="{ background: '#f5f5f5' }"
      >
        <el-table-column prop="businessType" label="业务类型" width="">
        </el-table-column>
        <el-table-column prop="customerId" label="客户" width="">
        </el-table-column>
        <el-table-column prop="business_order" label="业务单" width="">
          <template #default={row}>
            <el-button
              type="text"
              size="mini"
              @click="jump()"
              >{{ row.business_order }}</el-button
            >
          </template>
        </el-table-column>
        <el-table-column prop="salesman" label="业务员" width="">
        </el-table-column>
        <el-table-column prop="operation_time" label="操作时间" width="">
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
import { reactive, toRefs } from "vue";
import { getCurrentInstance } from "@vue/runtime-core";
// import  from "";
export default {
  components: {},
  setup() {
    const { proxy } = getCurrentInstance();
    const state = reactive({
      businessType: "",
      businessTypelist: [],
      salesmanId: "",
      salesmanlist: [],
      customerId: "",
      customerlist: [],
      followup_date: "",
      tableData: [
        {
          businessType: "跟进-电话",
          customerId: "上海客户",
          business_order: "XX主题",
          salesman: "王立",
          operation_time: "2021-8-21 12:23:08",
        },
        {
          businessType: "跟进-电话",
          customerId: "上海客户",
          business_order: "XX主题",
          salesman: "王立",
          operation_time: "2021-8-21 12:23:08",
        },
        {
          businessType: "跟进-电话",
          customerId: "上海客户",
          business_order: "XX主题",
          salesman: "王立",
          operation_time: "2021-8-21 12:23:08",
        },
      ],
    });
    const jump = () => {
      proxy.$router.push({
        path:"/follow_record",
        query:{
          isouter:1,
          isedit:1
        }
      });
    };

    const search = () => {
      console.log();
    };
    const resetForm = () => {
      console.log();
    };

    return {
      ...toRefs(state),
      search,
      resetForm,
      jump,
    };
  },
};
</script>

<style scoped>
.top_box {
  background-color: #fff;
  display: flex;
  justify-content: space-between;
  padding: 0 10px;
  box-sizing: border-box;
  margin-top: 10px;
}
.top_sel {
  margin-left: 10px;
}
.search_btn {
  margin-left: 10px;
}

.main_box {
  margin-top: 10px;
  padding: 0 10px;
  box-sizing: border-box;
}
</style>
<style>
.el-range-editor--mini.el-input__inner {
  margin-left: 10px;
}
</style>
